.signUpForm{
    height: 550px;
    border: 1px solid #dbdbdb;
    width: 450px;
    /* margin: 25px; */
    text-align: center;
    background-color: white;
}

.fbSignUp h3{
    color: #8E8E8E;
}

.signuppage_text{
    width: 258px;
    height: 36px;
    margin: 5px;
    border: 1px solid #dbdbdb;
    background-color: #fafafa;
    border-radius: 5px;
    padding-left: 5px;
}


.signuppage_button{
    font-size: medium;
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    font-weight: bold;
    color: white;
    border: 1px solid #0395f6;
    margin-top: 5px;
    width: 258px;
    height: 40px;
    background-color: #0395f6;
    border-radius: 5px;

}

.login_ordiv {
    display: flex;
    justify-content: center;
    margin-top: 10px;
}

.login_divide {
    width: 80px;
    height: 0px;
    margin-top: 10px;
    border:1px solid #DBDBDB;
}

.login_or {
    margin: 0px 10px;
    font-weight: bold;
    color: #8E8E8E;
}
