*{
    box-sizing: border-box;
}

body{
    background-color: #fafafa;
}

.loginpage_main{
    display: flex;

}
.loginpage_logo{
    width: 175px;
    margin-top: 25px;
}
.loginpage_rightcomponent{
    height: 380px;
    border: 1px solid #dbdbdb;
    width: 350px;
    margin: 25px;
    text-align: center;
    background-color: white;
}

.loginpage_signin{
    margin: 25px;

}
.loginpage_text{
    width: 258px;
    height: 36px;
    margin: 5px;
    border: 1px solid #dbdbdb;
    background-color: #fafafa;
    border-radius: 10px;
    padding-left: 5px;
}


.loginpage_button{
    font-size: medium;
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    font-weight: bold;
    color: white;
    border: 1px solid #0395f6;
    margin-top: 5px;
    width: 258px;
    height: 40px;
    background-color: #0395f6;
    border-radius: 5px;

}

.login_ordiv {
    display: flex;
    justify-content: center;
    margin-top: 10px;
}

.login_divide {
    width: 80px;
    height: 0px;
    margin-top: 10px;
    border:1px solid #DBDBDB;
}

.login_or {
    margin: 0px 10px;
    font-weight: bold;
    color: #8E8E8E;
}

.login__fb {
    color: #395185;
    font-weight: bold;
    margin-top: 30px;
}
.login_forgt {
    color: rgba(var(--fe0,0,55,107),1);
    font-size: 15px;
    line-height: 14px;
    margin-top: 12px;
    text-align: center;
    
}
.loginpage__signupoption {
    margin-top: 15px;
    height: 60px;
    width: 350px;
    background-color: white;
    border: 1px solid #DBDBDB;
    text-align: center;
    
}

.signin{
    font-size: 15px;
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}
