.navBar{
    float: left !important;
}

.post{
    height: 500px;
    border: 1px solid #dbdbdb;
    width: 500px;
    margin: 25px;
    text-align: center;
    background-color: white;
}

.messageSection{
    margin-top: 20px;
    border: 1px solid #dbdbdb;
}

.postImage{
    
    background-position: relative;
    height: 350px;
    width: 350px;
    background-size: cover;
    background-repeat: no-repeat;
}

.messageContainer{
    height: 500px;
    width: 550px;
    border: 1px solid #dbdbdb;
}

.messageHead{
    border: 1px solid #dbdbdb;
}
.messageContent{
    height: 360px;
}

.messageSend input{
    width: 480px;
    height: 38px;
    margin-left: 15px;
    border-radius: 10px;
}